.Settings {
  background-color: rgba(0, 0, 0, 0.75);
  bottom: 0;
  color: white;
  height: 0;
  left: 0;
  overflow: hidden;
  position: absolute;
  -webkit-transition: height 500ms;
  -o-transition: height 500ms;
  transition: height 500ms;
  width: 100%;
}

.Settings.is-open {
  height: 100%;
}

.Settings__content {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  padding: 10vh 10vw;
  width: 100vw;
}

.Settings__form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Settings__fieldset {
  padding: 0 10vw;
  margin: 2vh 0;
}

.Settings__legend {
  font-size: 125%;
  font-weight: bold;
  height: 0;
  position: relative;
  top: -12px;
}

.Settings__group {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Settings__field {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}

.Settings__label {
  -ms-flex: 1 1;
      flex: 1 1;
}

.Settings__input {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-left: 2vh;
}


